import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { TestPage } from '../test/test';
import { LoginService } from './loginService';
import {LoginModel} from './loginModel';
import { TokenModel } from './TokenModel';
import {ToastController} from 'ionic-angular';
import { ToastOptions } from 'ionic-angular/components/toast/toast-options'

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
  providers:[LoginService],
})
export class LoginPage {

  Token:TokenModel;
  loginModel = new LoginModel();
  toastOptions:ToastOptions;

  constructor(public navCtrl: NavController, public navParams: NavParams, private _service:LoginService, private _toast:ToastController) {
    this.toastOptions = {message : 'Please verify your credentials', duration:2000}
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }

  onLoginSuccesful(token:string){
    this.navCtrl.push(TestPage).then(()=>
  this.navCtrl.remove(0, this.navCtrl.getActive().index));
  console.log(token);
  }

  login(){
    this._service.login(this.loginModel.username,this.loginModel.password)
    .subscribe(token =>{this.Token = token,this.onLoginSuccesful(this.Token.access)},
    ()=><any>this._toast.create(this.toastOptions).present());
  }


}



